.contacts {
  background-color: $secondary-text-color;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: linear-gradient(
      rgba(17, 21, 24, 0.4),
      rgba(17, 21, 24, 0.4)
    ),
    url('../images/img/mobile/page-contacts/contacts-bg.png');
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  @media screen and (min-width: $mobile-general) {
    background-image: linear-gradient(
        rgba(17, 21, 24, 0.4),
        rgba(17, 21, 24, 0.4)
      ),
      url('../images/img/tablet/page-contacts/contacts-bg.png');
  }

  @media screen and (min-width: $tablet-general) {
    background-image: linear-gradient(
        rgba(17, 21, 24, 0.4),
        rgba(17, 21, 24, 0.4)
      ),
      url('../images/img/desktop/page-contacts/contacts-bg.png');
  }
  @media screen and (min-width: $desktop-up) {
    padding-bottom: 0;
  }
  &__container {
    padding-top: 50px;
    @media screen and (min-width: $tablet-general) {
      padding-bottom: 20px;
    }
    @media screen and (min-width: $desktop-up) {
      padding-top: 100px;
    }
  }

  &__title {
    margin-left: auto;
    margin-right: auto;
    font-weight: 800;
    font-size: 14px;
    line-height: 1.29;
    text-align: center;
    letter-spacing: 0.03em;
    color: $secondary-title-text-color;
    margin-bottom: 30px;

    @media screen and (min-width: $tablet-general) {
      font-size: 26px;
      line-height: 1.31;
    }
    @media screen and (min-width: $desktop-up) {
      width: 1090px;
      font-size: 46px;
      line-height: 1.3;
      margin-bottom: 50px;
    }
  }

  &__description {
    font-weight: 600;
    font-size: 12px;
    line-height: 1.25;
    text-align: center;
    color: $third-text-color;
    margin-bottom: 30px;

    @media screen and (min-width: $tablet-general) {
      font-size: 14px;
      line-height: 17px;
    }
    @media screen and (min-width: $desktop-up) {
      font-size: 30px;
      line-height: 1.23;
      margin-bottom: 50px;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
    @media screen and (min-width: $tablet-general) {
      margin-bottom: 31px;
    }
  }

  &__input {
    width: 280px;
    height: 48px;
    font-family: Montserrat;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.21;
    letter-spacing: 0.08em;
    background-color: transparent;
    border: 1px solid white;
    &::placeholder {
      padding-left: 28px;
      color: $third-text-color;
    }

    @media screen and (min-width: $tablet-general) {
      width: 390px;
    }
    @media screen and (min-width: $desktop-up) {
      width: 478px;
      height: 70px;
      font-size: 20px;
      line-height: 1.2;
      margin-bottom: 30px;
    }
  }
}
.contacts__label + .contacts__label {
  margin-top: 20px;
}
.accent-words {
  color: $selection-title-text-color;
}
